import { Node, Button, Label, sp } from "cc";
import { Panel } from "../../manager/GameRoot";
import base from "../../net/proto/BaseUtils";
import { Protocol } from "../../net/Protocol";
import { PlayerData } from "../player/PlayerData";
import { Session } from "../../net/Session";
import { MainPanel } from "../main/MainPanel";
import { GameScene } from "../fight/GameScene";
import { battleDataMgr } from "./BattleDataMgr";
import { ResMgr } from "../../manager/ResMgr";
import { MainUI3D } from "../main/MainUI3D";

export class BattleSettlementPanel extends Panel{
    protected prefab: string = "prefabs/battle/BattleSettlementPanel";
    private okBtn: Button;
    private effect: sp.Skeleton;
    private roomId:string;
    private data: base.BattleRoomOpOutput_Settlement;
    private playerAId: string;
    protected onLoad(): void {
        this.effect = this.find("effect", sp.Skeleton);
        this.okBtn = this.find("okBtn", Button);
        this.okBtn.node.on(Button.EventType.CLICK, this.onBtnClick, this);
    }
    protected onShow(): void {
        
    }
    public flush(roomId:string, playerAId:string, data: base.BattleRoomOpOutput_Settlement): void {
       this.roomId = roomId;
       this.playerAId = playerAId;
       this.data = data;
       this.playEffect();
    }

    protected onHide(...args: any[]): void {
        
    }

    private onBtnClick(btn: Button): void {
        switch (btn) {
            case this.okBtn:
                if (!this.data.leavePlayerId || this.data.leavePlayerId.length < 1) {
                    let data = new Protocol.battleroomleave();
                    data.server = PlayerData.server;
                    data.roomId = this.roomId;
                    Session.Send(data);
                    console.log("无玩家主动退出房间 发送离开房间---->")
                }
                this.Hide();
                MainPanel.Show();
                battleDataMgr.clearBattleData();
                GameScene.Hide();
                MainUI3D.Show();
                break;
            default:
                break;
        }
    }
    
    protected playEffect(): void {
        this.okBtn.node.active = false;
        let effectName: string;
        if (this.playerAId == this.data.winnerPlayerId) {
            effectName = "ui_result_win";
        } else {
             if (this.playerAId == this.data.leavePlayerId) {
                effectName = "ui_result_run";
             } else {
                effectName = "ui_result_lost";
             }
        }
        ResMgr.LoadResAbSub(`spine/${effectName}/${effectName}`, sp.SkeletonData, (res: sp.SkeletonData) => {
            this.effect.skeletonData = res;
            this.effect.setAnimation(0, "show", false);
            this.effect.setCompleteListener(()=>{
                if (this.effect.animation == "show") {
                    this.effect.setAnimation(0, "loop", true);
                    this.okBtn.node.active = true;
                }
            });
        });
        
    }
}